<template>
  <div class="container-fluid" style="minHeight:100vh;">
    <div class="row py-4 text-center grey-text">
      <span>You cannot manage your posts yet, this is a Demo version</span>
    </div>
    <div class="row" v-if="arePosts">
      <div v-for="(post, index) in filteredPosts" :key="index">
        <div class="col s12 m5 l3">
          <div class="p-0 m-0">
            <div class="card" v-if="post.user.email == user.email">
              <div class="card-image" v-if="post.images">
                <img :src="post.images[0].url" />
                <span class="card-title">{{ post.title }}</span>
              </div>
              <div class="card-content">
                <p>
                  {{
                    post.description.length > 60
                      ? post.description.substring(0, 60) + "..."
                      : post.description
                  }}
                </p>
                <br />
                <span class="mb-3" style="fontSize:1.2em;"
                  >â‚¡{{ post.price }}</span
                >
              </div>
              <div class="card-action">
                <router-link
                  :to="{ name: 'product', params: { postId: post.id } }"
                  >See more
                </router-link>
                <span class="badge">{{ getCategory(post.category) }}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-else class="row py-4 text-center grey-text">
      <span>You have not posts yet</span>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters, mapMutations, mapState } from "vuex";
export default {
  name: "MyPosts",
  data() {
    return {
      categories: [
        {
          value: 1,
          name: "Art",
        },
        {
          value: 2,
          name: "Electronics",
        },
        {
          value: 3,
          name: "Vehicles",
        },
        {
          value: 3,
          name: "Sports",
        },
        {
          value: 4,
          name: "Real Estate",
        },
      ],
      filteredPosts: [],
    };
  },
  computed: {
    ...mapState("posts", ["posts"]),
    ...mapState("user", ["user"]),
    arePosts() {
      return this.filteredPosts.length > 0 && this.user;
    },
    postsLength() {
      return this.posts ? this.posts.length : 0;
    },
  },
  methods: {
    ...mapMutations(["toggleLoader", "startLoader"]),
    ...mapActions("posts", ["getPosts"]),
    getCategory(index) {
      return this.categories[index - 1].name;
    },
    filterPosts() {
      this.posts.forEach((post) => {
        if (post.user.email == this.user.email) {
          this.filteredPosts.push(post);
        }
      });
    },
  },
  created() {
    this.getPosts();
    this.startLoader();
  },
  beforeMount() {
    if (this.postsLength > 0) {
      this.filterPosts();
    }
  },
  mounted() {
    if (this.postsLength > 0) {
      this.toggleLoader();
    }
  },
  watch: {
    postsLength: function(value) {
      this.filterPosts();
      this.toggleLoader();
    },
  },
};
</script>
<style scoped>
.card {
  height: 430px !important;
}
.card-content {
  max-height: 130px;
  min-height: 130px;
}
.card-image .card-title {
  width: 100%;
  font-weight: 400;
  text-transform: capitalize;
  box-shadow: inset 0px -70px 20px -15px #4e4e4ebb;
}
.card-image img {
  max-height: 240px;
  min-height: 240px;
  object-fit: cover;
}
</style>
